import Vue from 'vue'
import Router from 'vue-router'
import About from '@/pages/about/about'
import Skill from '@/pages/skill/skill'
import Expirence from '@/pages/expirence/expirence'
import Program from '@/pages/program/program'
import Info from '@/pages/info/info'
import ExpirenceOne from '@/components/expirence-one/expirence-one'
import ExpirenceTwo from '@/components/expirence-two/expirence-two'

Vue.use(Router)

export default new Router({
  linkActiveClass: 'active',
  routes: [
    {
      path: '/about',
      name: 'about',
      component: About
    }, {
      path: '/skill',
      name: 'skill',
      component: Skill
    }, {
      path: '/expirence',
      name: 'expirence',
      component: Expirence,
      children: [{
        path: 'expirenceone',
        component: ExpirenceOne
      }, {
        path: 'expirencetwo',
        component: ExpirenceTwo
      }, {
        path: '/expirence',
        redirect: '/expirence/expirenceone'
      }]
    }, {
      path: '/program',
      name: 'program',
      component: Program
    }, {
      path: '/contact',
      name: 'contact',
      component: Info
    }, {
      path: '/',
      redirect: '/about'
    }
  ]
})



