<template>
  <div class="navs">
    <div class="navs-item" :class="{'active' : currentIndex === index}" v-for="(item, index) in items" :key="item.id" @click="selItem(index, item.url)">
      <span :class="item.cls" class="al"></span>
      <span>{{item.alias}}</span>
    </div>
  </div>
</template>

<script type="text/javascript">
export default{
  data () {
    return {
      items: [
        {
          cls: 'al-about',
          alias: '关于',
          url: '/about'
        }, {
          cls: 'al-skill',
          alias: '技能',
          url: '/skill'
        }, {
          cls: 'al-expirence',
          alias: '经历',
          url: '/expirence'
        }, {
          cls: 'al-program',
          alias: '项目',
          url: '/program'
        }, {
          cls: 'al-contact',
          alias: '联系',
          url: '/contact'
        }
      ],
      currentIndex: 0
    }
  },
  methods: {
    selItem (index, url) {
      console.log(url)
      this.currentIndex = index
      this.$router.push({
        path: url
      })
    }
  }
};
</script>

<style lang="stylus">
  .navs{
    flex: 0 0 60px
    width: 60px
    height: 100vh
    box-sizing: border-box
    display: flex
    flex-direction: column
    justify-content: center
    align-items: center
    background-color: #fff
    // box-shadow: -5px 0 5px rgba(255, 255, 255, .1)
    box-shadow: -1px 0 1px #eee
    .navs-item{
      width: 100%;
      display: flex
      flex-direction: column
      justify-content: center
      align-items: center
      color: rgba(0, 0, 0, .6)
      font-size: 12px
      margin-bottom: 60px
      cursor: pointer
      .al{
        font-size: 16px
        margin-bottom: 2px
      }
    }
    .active{
      color: #31c27c
    }
  }
  @media screen and (max-width: 768px) {
    .navs{
      width: 100vw
      height: auto
      flex-direction: row
      padding: 5px 0
      // background-color: rgba(0, 0, 0, 0.1)
      // border-top: 0.5px solid #ccc
      box-shadow: 0 -1px 1px #eee
      .navs-item{
        width: 20%
        margin-bottom: 0
        // border-right: 0.5px solid #ccc
        box-sizing: border-box
        .al-program{
          font-size: 15px
        }
      }
      .navs-item:last-child{
        border-right: none
      }
    }
  }
</style>



